  border-radius: 4px;
  transition: all 0.3s ease;
}
.help-panel-content .help-column ul li a:hover, .help-panel-content .help-column ul li a:focus {
  background-color: #1263a0 !important;
  color: #fff !important;
  text-decoration: none;
}
