/*****************************************************************
 *  MOBILE MENU STYLES
 *  File:  _mobile-menu.scss
 *****************************************************************/
/* Mobile Menu Overlay - Hidden by default on all screens */
.mobile-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease, visibility 0.3s ease;
  /* Show when active */
}
.mobile-menu-overlay.active, .media-library-view--widget .view-header a.mobile-menu-overlay.is-active {
  visibility: visible;
  opacity: 1;
}

/* Backdrop (dark overlay) */
.mobile-menu-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

/* Mobile Menu Panel */
.mobile-menu-panel {
  position: absolute;
  top: 0;
  right: 0;
  width: 320px;
  max-width: 85vw;
  height: 100%;
  background-color: #fff;
  box-shadow: -2px 0 10px rgba(0, 0, 0, 0.1);
  transform: translateX(100%);
  transition: transform 0.3s ease;
  overflow-y: auto;
}
.mobile-menu-overlay.active .mobile-menu-panel, .media-library-view--widget .view-header a.mobile-menu-overlay.is-active .mobile-menu-panel {
  transform: translateX(0);
}

/* Mobile Menu Header */
.mobile-menu-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 1px solid #e9ecef;
}
.mobile-menu-header .mobile-menu-logo img {
  height: 40px;
  width: auto;
}
.mobile-menu-header .mobile-menu-close {
  background: none;
  border: none;
  font-size: 1.5rem;
  color: #495057;
  cursor: pointer;
  padding: 0.5rem;
  transition: color 0.3s ease;
}
.mobile-menu-header .mobile-menu-close:hover {
  color: #1263a0;
}

/* Mobile Menu Navigation */
.mobile-menu-nav {
  padding: 1rem 0;
}

/* Mobile Navigation Items */
.mobile-nav-item {
  border-bottom: 1px solid #e9ecef;
}
.mobile-nav-item:last-child {
  border-bottom: none;
}

.mobile-nav-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.5rem;
  color: #212529;
  text-decoration: none;
  transition: background-color 0.3s ease;
}
.mobile-nav-link:hover {
  background-color: #f8f9fa;
}
.mobile-nav-link.no-submenu {
  display: block;
  padding: 1rem 1.5rem;
  color: #212529;
  text-decoration: none;
  transition: background-color 0.3s ease;
}
.mobile-nav-link.no-submenu:hover {
  background-color: #f8f9fa;
  color: #1263a0;
}
.mobile-nav-link .menu-text-link {
  color: #212529;
  text-decoration: none;
  flex: 1;
}
.mobile-nav-link .menu-text-link:hover {
  color: #1263a0;
}
.mobile-nav-link .menu-toggle {
  background: none;
  border: none;
  padding: 0.5rem;
  color: #6c757d;
  cursor: pointer;
  transition: transform 0.3s ease;
}
.mobile-nav-link .menu-toggle[aria-expanded=true] .menu-icon {
  transform: rotate(45deg);
}

/* Mobile Submenu */
.mobile-submenu {
  display: none;
  background-color: #f8f9fa;
}
.mobile-submenu.show {
  display: block;
}

.mobile-submenu-item {
  border-top: 1px solid #e9ecef;
}
.mobile-submenu-item:first-child {
  border-top: none;
}

.mobile-submenu-link {
  display: block;
  padding: 0.75rem 1.5rem 0.75rem 2.5rem;
